// components/GaleriaTextos.tsx
export default function GaleriaTextos({ textos }: { textos: Array<{ titulo: string; contenido: string }> }) {
  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-6 max-w-4xl mx-auto">
      {textos.map((texto, index) => (
        <div key={index} className="bg-white p-6 rounded-lg shadow-md border border-gray-100 hover:shadow-lg transition-shadow">
          <h3 className="text-lg font-semibold mb-3 text-gray-800">{texto.titulo}</h3>
          <p className="text-gray-600 leading-relaxed">{texto.contenido}</p>
        </div>
      ))}
    </div>
  );
}